      --cols: repeat(auto-fit, minmax(400px, 1fr)); } }

.copy > :first-child {
  margin-top: 0; }

.copy > :last-child {
  margin-bottom: 0; }

.copy code {
  font-size: 115%;
  padding-left: 0.125em;
  padding-right: 0.125em;
  font-weight: 500; }

.copy blockquote {
  font-size: 112.5%;
