.copy table {
  border-collapse: collapse;
  border: 1px solid var(--color-alpha); }
  .copy table th,
  .copy table td {
    padding: .25rem .5rem;
    border: 1px solid var(--color-alpha);
    text-align: left; }
  .copy table th  {
    font-size: 112.5%; }

.copy iframe {
  max-width: 100%; }

.table-wrapper {
  max-width: 100%;
  overflow: auto; }
